.container{
  background: $light-grey;
  border: 1px solid $light-grey-border;
  border-radius: 5px;
  padding: 1rem;
  margin-bottom: 1rem;
  overflow: scroll
}
.container__header{
  border-bottom: 1px solid $light-grey-border;
  padding: 1rem;

  label {
    cursor: pointer;
    font-size: 1rem;
  }
  > :last-child {
    align-items: center;
    display: flex;
  }
}
.container__message{
  color: $light-color;
  margin: 2rem auto;
  text-align: center;
}
.container__footer{
  border-top: 1px solid $light-grey-border;
  padding: 1rem 1rem 0 1rem;
}
