.todo{
  align-items: center;
  display: flex;
  cursor: pointer;
  padding: 1rem;
  transition: background .3s ease;

  p, input {
    margin: 0;
  }

  > :first-child {
    margin-right: 1rem;
  }
  &:hover{
    background: #f0f0f0;
  }
}
.todo__subtext{
  color: #999;
}
.todo-completed{
  p, .todo-subtext{
    color: $light-color;
    text-decoration: line-through;
  }
}
